

import store from '@/store'
import axios from 'axios'
import { Message } from 'element-ui'
import util from '@/libs/util'
import router from '@/router'


// 
function errorCreate(msg) {
    const error = new Error(msg)
    errorLog(error)
    throw error
}

// 
function errorLog(error) {
    // 
    store.dispatch('d2admin/log/push', {
            message: '',
            type: 'danger',
            meta: {
                error
            }
        })
        // 
    if (process.env.NODE_ENV === 'development') {
        util.log.danger('>>>>>> Error >>>>>>')
        console.log(error)
    }
    // 
    Message({
        message: error.message,
        type: 'error',
        duration: 5 * 1000
    })
}

//  axios 
const service = axios.create({
    baseURL: '', //process.env.VUE_APP_API,
    timeout: 5000 // 
})

// 
service.interceptors.request.use(
    config => {
        // 
        const token = util.cookies.get('token_wharf')
            // token-- ['X-Token']key 
        config.headers['Authorization'] = 'Bearer ' + token
            //config.headers['Authorization'] = 'Bearer eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpc3MiOiJodHRwOi8vMTAuMTAuMTAuNjA6MjE0L2xvZ2luIiwiaWF0IjoxNTc2MDI5Nzk3LCJleHAiOjE1NzYwNDA1OTcsIm5iZiI6MTU3NjAyOTc5NywianRpIjoiZkFnNHYzdlh6clBjZ09WSyIsInN1YiI6MSwicHJ2IjoiODdlMGFmMWVmOWZkMTU4MTJmZGVjOTcxNTNhMTRlMGIwNDc1NDZhYSJ9.DLrkY5y19q0y26BoV2-kdki8_SPVOwCiJqsUo6_2TE8'
        return config
    },
    error => {
        // 
        console.log(error)
        return Promise.reject(error)
    }
)

// 
service.interceptors.response.use(
    response => {
        // dataAxios  axios  data
        const dataAxios = response.data
        // console.log(dataAxios)
            // 
        const { code } = dataAxios
        //  code 
        if (code === undefined) {
            //  code   D2Admin 
            return dataAxios
        } else {
            //  code  
            switch (code) {
                case 0:
                    return dataAxios.data
                case 200:
                    // [  ] code === 0 
                    return dataAxios
                case 201:
                    // [  ]  code
                    return response
                case 401:
                    //
                    router.push({ path: "/login" })
                case 500:
                    Message({
                        message: dataAxios.msg,
                        type: 'error'
                    })
                // [  ] code === 0 
                     return dataAxios
                default:
                    //  code
                    // errorCreate(`${dataAxios.msg}: ${response.config.url}`)
                    Message({
                        message: dataAxios.msg,
                        type: 'error'
                    })
                    break
            }
        }
    },
    error => {
        if (error && error.response) {
            switch (error.response.status) {
                case 400:
                    error.message = '';
                    break
                case 401:
                    error.message = '';
                    //
                    router.push({ path: "/login" })
                    break
                case 403:
                    error.message = '';
                    break
                case 404:
                    error.message = `: ${error.response.config.url}`;
                    break
                case 408:
                    error.message = '';
                    break
                case 500:
                    error.message = '';
                    break
                case 501:
                    error.message = '';
                    break
                case 502:
                    error.message = '';
                    break
                case 503:
                    error.message = '';
                    break
                case 504:
                    error.message = '';
                    break
                case 505:
                    error.message = 'HTTP';
                    break
                default:
                    break
            }
        }
        errorLog(error)
        return Promise.reject(error)
    }
)

export default service