

//  
import util from '@/libs/util.js'
// const loginData_wharf = JSON.parse(util.cookies.get('loginData_wharf'));
// console.log("-----", util.cookies.get('loginData_wharf'));

function m() {
    var loginData_wharf = {};
    try {
        loginData_wharf = JSON.parse(util.cookies.get('loginData_wharf'))
    } catch (d) {}
    var menuJson = [];
    //******************************************************************************
    if (loginData_wharf.media.mediaList == 1 || loginData_wharf.media.adPositionList == 1 || loginData_wharf.media.trafficplatform == 1 ) {
        if (loginData_wharf.media.trafficplatform == 1) {
            menuJson = menuJson.concat([{
                title: '',
                icon: 'folder-o',
                children: [
                    { path: '/mediaManage/trafficplatform', title: '' },
                    { path: '/mediaManage/trafficplatform/editTrafficplatform', title: '', hide: true },
                    { path: '/mediaManage/trafficplatform/editTrafficplatform', title: '', hide: true },
                ]
            }])
        }
        if (loginData_wharf.media.mediaList == 1) {
            menuJson = menuJson.concat([{
                title: '',
                icon: 'folder-o',
                children: [
                    { path: '/mediaManage/mediaList', title: '' },
                    { path: '/mediaManage/mediaList/editMedias', title: '', hide: true },
                    { path: '/mediaManage/mediaList/editMedias', title: '', hide: true },
                    { path: '/mediaManage/mediaList/shareMedia', title: '', hide: true },
                ]
            }])
        }
        if (loginData_wharf.media.adPositionList == 1) {
            menuJson = menuJson.concat([{
                title: '',
                icon: 'folder-o',
                children: [
                    { path: '/mediaManage/adsenseList', title: '' },
                    { path: '/mediaManage/adsenseList/editAdsenseList', title: '', hide: true },
                    { path: '/mediaManage/adsenseList/shareAdsense', title: '', hide: true }
                ]
            }])
        }
    }
    if(loginData_wharf.dataEntry.dataEntryChildren == 1){
        
        if(loginData_wharf.dataEntry.dataEntryChildren == 1) {
            menuJson = menuJson.concat([{
                title: '',
                icon: 'folder-o',
                children: [
                    { path: '/dataSearch/dataEntryChildren', title: '' },
                ]
            }])
        }
    }

    if (loginData_wharf.dataForm.entryData == 1||loginData_wharf.dataForm.overallData == 1) {
        if(loginData_wharf.dataForm.overallData == 1) {
            menuJson = menuJson.concat([{
                title: '',
                icon: 'folder-o',
                children: [
                    { path: '/dataFormReport/overallData', title: '' },
                    { path: '/dataFormReport/overallData/releaseData', title: '',hide: true },
                ]
            }])
        }
        if(loginData_wharf.dataForm.entryData == 1) {
            menuJson = menuJson.concat([{
                title: '',
                icon: 'folder-o',
                children: [
                    { path: '/dataFormReport/entryData', title: '' },
                ]
            }])
        }
    }
    if (loginData_wharf.account.accountMeg == 1 || loginData_wharf.account.userMeg == 1) {
        menuJson = menuJson.concat([{
            title: '',
            icon: 'folder-o',
            children: [
            ]
        }])
        
        if (loginData_wharf.account.accountMeg == 1) {
            menuJson = menuJson.concat([{
                title: '',
                icon: 'folder-o',
                children: [
                    { path: '/accountManage/accountMeg', title: '' },
                    { path: '/accountManage/accountMeg/editAccounts', title: '', hide: true },
                    { path: '/accountManage/accountMeg/editAccounts', title: '', hide: true },
                ]
            }])
        }
        if (loginData_wharf.account.userMeg == 1) {
            menuJson = menuJson.concat([{
                title: '',
                icon: 'folder-o',
                children: [
                    { path: '/accountManage/roleMeg', title: '' },
                    { path: '/accountManage/roleMeg/editRoles', title: '', hide: true },
                    { path: '/accountManage/roleMeg/editRoles', title: '', hide: true },
                ]
            }])
        }

    }
    

    var map = {};
    var arr = [];
    for (var o of menuJson) {
        if (!map.hasOwnProperty(o.title)) {
            map[o.title] = o;
            const val = map[o.title].children;
            map[o.title].children = [val];
        } else {
            map[o.title].children.push(o.children);
        }
    }

    function transform(obj) {
        for (var item in obj) {
            arr.push(obj[item]);
        }
    }
    transform(map);
    for (var a = 0; a < arr.length; a++) {
        arr[a].children = arr[a].children.reduce(function(a, b) { return a.concat(b) });
    }



    return arr




    // //******************************************************************************



}
export default m