<template>
<div class="alls">
  <el-dropdown size="small" class="d2-mr">
    <span class="btn-text">{{info.name ? ` ${info.name}` : ''}}</span>
    <el-dropdown-menu slot="dropdown">
      <!-- <el-button @click="dialogVisible = true" style="border:none;font-size:14px;display:block;"></el-button> -->
      <el-dropdown-item @click.native="dialogVisible = true" style="font-size:14px">
        <i class="fa fa-pencil" aria-hidden="true"></i>
        
      </el-dropdown-item>
      <el-dropdown-item @click.native="logOff" style="font-size:14px">
         <d2-icon name="power-off" class="d2-mr-5"/>
        
      </el-dropdown-item>
    </el-dropdown-menu>
  </el-dropdown>
   <el-dialog title="" :visible="dialogVisible" :before-close="closeDlg" width="500">
        <el-form :model="mainForm"  ref="mainForm"  label-width="120px" :rules="rule ">
          <el-form-item  label=":">
             {{this.user_name}}
          </el-form-item>
          <el-form-item  label=":">
             {{this.user_mobile}}
          </el-form-item>
          <el-form-item  label=":" prop="password">
              <el-input v-model="mainForm.password" placeholder="" style="width: 220px;" />
          </el-form-item>
           <el-form-item  label=":" prop="new_password">
              <el-input v-model="mainForm.new_password" placeholder="" style="width: 220px;" />
          </el-form-item>
          <el-form-item  label=":" prop="new_password_confirmation">
              <el-input v-model="mainForm.new_password_confirmation" placeholder="" style="width: 220px;" />
          </el-form-item>
        </el-form>
        <div style="font-size: 8px;margin-left: 50px;">:   </div>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="addSave"> </el-button>
        </div>
    </el-dialog>
</div>
</template>

<script>
import util from '@/libs/util'
import { mapState, mapActions } from 'vuex'
export default {
   data(){
       var validatePass1 = (rule, value, callback) => {
            if (value === "") {
                callback(new Error(""));
            } else {
                if (this.mainForm.new_password !== "") {
                this.$refs.mainForm.validateField("new_password_confirmation");
                }
                callback();
            }
            };
        var validatePass2 = (rule, value, callback) => {
        if (value === "") {
            callback(new Error(""));
        } else if (value !== this.mainForm.new_password) {
            callback(new Error(""));
        } else {
            callback();
        }
        };
      return {
          dialogVisible: false,
          mainForm:{
             name:"",//
             mobile:"",//
             password:"",//
             new_password:"",//
             password_confirmation:"",//
          },
          rule:{
              name: [
                { required: true, message: "", trigger: "blur" },
                { min: 0, max: 20, message: '20', trigger: 'blur' },
             ],
             mobile:[
                { pattern:/^[1][3,4,5,6,7,8,9][0-9]{9}$/, message: '',trigger:'blur',required: true }
             ],
             password:[
               {required: true,messgae:"", trigger: "blur"},
             ],
             new_password:[
               {required: true,validator: validatePass1, trigger: "blur"},
               { pattern:/[a-z]/, message: '',trigger:'blur',required: true },
               { pattern:/[A-Z]/, message: '',trigger:'blur',required: true },
               { pattern:/\W/, message: '',trigger:'blur',required: true },
               { pattern:/[1,2,3,4,5,6,7,8,9,0]/, message: '',trigger:'blur',required: true },
               { min: 6 , message: '6', trigger: 'blur' },
               { max:18 , message: '18', trigger: 'blur' },
             ],
             new_password_confirmation:[
               {required: true,validator: validatePass2, trigger: "blur"},
               { min: 6, max:20 , message: '6', trigger: 'blur' },
             ]
          },
         
          user_name:"",
          user_mobile:"",
      }
  },
  computed: {
    ...mapState('d2admin/user', [
      'info'
    ]),
  },
  created(){
      this.user_name = util.cookies.get('userAll_name_wharf');
      this.user_mobile = util.cookies.get('userAll_mobile_wharf')

  },
  methods: {
    ...mapActions('d2admin/account', [
      'logout'
    ]),
    /**
     * @description 
     */
    logOff () {
      this.logout({
        confirm: true
      })
    },
    closeDlg(){
       this.dialogVisible=false;
    },
    async addSave(){
       this.$refs.mainForm.validate( valid => { 
            console.log('00')
            if( valid ){
                console.log('1')
                this.submission()
            }else{
              this.$message({
                type: 'info',
                message: ''
              });
            } 
              
        })
    },
     async submission(){
        let params={
            new_password:this.mainForm.new_password,
            password:this.mainForm.password,
            new_password_confirmation:this.mainForm.new_password_confirmation
        }
        const {data: res} = await this.$http.post(this.baseUrl.ip1 + '/my/update',params);
        // console.log(data)
        // if(code == 200){
        this.$message.success('')
        this.dialogVisible=false;
        // }else{
        //    this.$message.error('')
        // }
      },
  }
}
</script>
<style lang="scss" scoped>
  
// .alls{
  ::v-deep .el-dialog__body {
    padding: 25px 20px;
    color: #606266;
    font-size: 14px;
    word-break: break-all;
    border-bottom: 1px solid #DCDFE6;
}

// }
</style>

<vue-filename-injector>
export default function (Component) {
  Component.options.__source = "src/layout/header-aside/components/header-user/index.vue"
}
</vue-filename-injector>
